.anim_gradient {
    /* background: linear-gradient(135deg, rgb(132, 18, 189) 10%, rgb(33, 113, 252) 100%); */
    background: rgb(63, 16, 126);
    padding: 5px;
}

.grid-container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 10px;
    color: rgb(255, 255, 255);
}

.item {
    background-color: rgba(0, 0, 0, 0.5);
    padding: 10px;
    outline: rgba(0, 0, 0, 0.333) 5px outset;
}

.wide-item {
    grid-column: span 2;
}

.subgrid-header{
    text-align: center;
    color: rgb(255, 255, 255);
}

.subgrid-column-header{
    text-align: center;
    font-weight: bold;
    font-size: large;
    background-color: rgba(0, 0, 0, 0.75);
}

.adjustment{
    margin-right: 5px;
}

.subgrid-container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
}

.element{
    background-color: rgba(0, 0, 0, 0.25);
    margin: 2.5px;
    padding: 2.5px;
}

.inline-header{
    margin-top: 10px;
}

.income{
    background-color: rgba(0, 191, 0, 0.5);
}
.expenses{
    background-color:  rgba(191, 0, 0, 0.5);
}
.important{
    color: red;
}
.output{
    color: white;
    float: right;
    width: 100px;
    text-align: center;
    /* outline: black solid 1px; */
    /* margin: 2.5px; */
}
.input-label{
    margin: 2.5px;
    padding: 2px;    
    background-color: rgba(0, 0, 0, 0.25);
}

.input-box{
    width: 100px;
    background-color: rgba(255, 255, 255, 0.125);
    float: right;
    color: rgb(151, 152, 255);
    text-align: center;
    border: none;
    margin: 2.5px;
    padding: 2px;  
}

.calculate_btn{
    margin-top: 25px;
    border: outset 5px rgba(0, 0, 0, 0.333);
    color: white;
    padding: 15px 45px;
    text-align: center;
    text-decoration: none;
    /* display: inline-block; */
    font-size: 24px;
    border-radius: 10px;
}
.calculate_btn{
    background-color: purple;
}
  
.calculate_btn:hover{
    background-color:rgb(102, 0, 128);
}
  
.calculate_btn:focus{
    background-color:darkslateblue;
}
  
.calculate_btn:active {
    color: rgb(124, 124, 124);
    border: inset 5px rgba(0, 0, 0, 0.333);
}

.navbar {
    color: white;
    background-color: rgb(21, 0, 41);
    overflow: hidden;
    position: fixed;
    bottom: 0;
    width: 100%;

  }