.flexbox{
    background: linear-gradient(90deg, rgb(0, 43, 83) 0%, rgb(21, 0, 41) 50%);
    background-size : "400% 400%";
    animation : "gradient 5s ease infinite";

    text-decoration: none;
    height: 60px;
    font-weight: bold;
    /* background-color: black; */
    color: whitesmoke;
    align-items: center;
    justify-content: space-between;
    /* text-align: center; */
    /* position: relative; */
    padding-inline: 10px;
    display:flex;
    /* border: red solid 1px; */
}
@keyframes gradient {
    0% {
        background-position: 50% 0%;
    }
    50% {
    background-position: 50% 100%;
    }
    100% {
    background-position: 50% 0%;
    }
}

.flex-item{
    /* border: 1px solid green; */
    position: relative;
    font-size: 30px;
    /* margin: 40px; */
}


  